import {
    REQUEST,
    START,
    FAILED,
    RECEIVE,
    RATE,
    BUDGET,
    UPDATE,
    DAY,
    MONTH,
    YEAR,
    AMOUNT, CURRENCY
} from '../constants';
import {
    dateYear, dateMonth, dateDay
} from '../share/share';

import {createSelector} from 'reselect'

const initialState = {
    isInvalid: false,
    isLoading: false,
    day: dateDay,
    month: dateMonth,
    year: dateYear,
    amount: 0,
    currency: 'USD'
};

export default (state = initialState, action) => {
    const {type, payload} = action;
    switch (type) {
        case REQUEST + START:
            return {
                ...state,
                isInvalid: false,
                isLoading: true
            }
        case RECEIVE + RATE:
            return {
                ...state,
                isInvalid: false,
                current: payload,
                isLoading: false
            }

        case RECEIVE + BUDGET:
            return {
                ...state,
                isInvalid: false,
                current : payload,
                isLoading: false
            }

        case REQUEST + FAILED:
            return {
                ...state,
                isInvalid: true,
                isLoading: false,
                error  : payload,
            }
        case UPDATE + CURRENCY:
            return {
                ...state,
                currency: payload,
            }
        case UPDATE + AMOUNT:
            return {
                ...state,
                amount : payload,
            }
        case UPDATE + DAY :
            return {
                ...state,
                day : payload,
            }
        case UPDATE + MONTH :
            return {
                ...state,
                month : payload,
            }
        case UPDATE + YEAR :
            return {
                ...state,
                year : payload,
            }
        case UPDATE + RATE :
            return {
                ...state,
                current : payload,
            }
        default:
            return state;
    }
};

export const stateSelector = (state) => state['rate'];
export const currentSelector = createSelector(stateSelector, (rate) => rate['current']);




